import { ModuleWithProviders } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AuthGuard } from './shared/guard/auth.guard';
import { ErrorpageComponent } from './shared/shared-services-and-components-module/components/errorpage/errorpage.component';


const routes: Routes = [
  { path: 'home', loadChildren: './private/private.module#PrivateModule', canActivate: [AuthGuard] },
  { path: 'user', loadChildren: './public/public.module#PublicModule' },
  { path: '', redirectTo: 'home', pathMatch: 'full' },
  { path: '404', component: ErrorpageComponent, pathMatch: 'full' },
  { path: '**', redirectTo: '404', pathMatch: 'full' }
];

export const routing: ModuleWithProviders = RouterModule.forRoot(routes);
