import { Injectable } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';

@Injectable()
export class ValidatorService {
    constructor() { }

    /**
     * validateUsername
     * @param control
     * @returns {any}
     */
    validateUsername(control: FormControl): { [s: string]: boolean } {
        let pattern = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

        if (!pattern.test(control.value)) {
            return { email: true };
        }
        return null;
    }

    /**
     * validatePassword
     * @param control
     * @returns {any}
     */
    validatePassword(control: FormControl): { [s: string]: boolean } {
        let pattern = /^(?=.*\d)(?=.*[a-z])(?=.*\W)(?=.*[A-Z]).{8,19}$/
        if (!pattern.test(control.value)) {
            return { password: true };
        }
        return null;
    }

    checkIfMatchingPasswords(passwordKey: string, passwordConfirmationKey: string) {
        return (group: FormGroup) => {
            let passwordInput = group.controls[passwordKey],
                passwordConfirmationInput = group.controls[passwordConfirmationKey];
            if (passwordInput.value !== passwordConfirmationInput.value) {
                return passwordConfirmationInput.setErrors({ notEquivalent: true })
            } else {
                return passwordConfirmationInput.setErrors(null);
            }
        };
    }

    validateEmail(control: FormControl) {
        let EMAIL_REGEXP = /^(|(([A-Za-z0-9]+_+)|([A-Za-z0-9]+\-+)|([A-Za-z0-9]+\.+)|([A-Za-z0-9]+\++))*[A-Za-z0-9]+@((\w+\-+)|(\w+\.))*\w{1,63}\.[a-zA-Z]{2,6})*$/i;
        if (control.value !== '') {
            return EMAIL_REGEXP.test(control.value) ? null : {
                validateEmail: {
                    valid: false
                }
            };
        } else {
            return true;
        }

    }

    validatePercentage(item) {
        if (item.value) {
            return item.value >= 0 && item.value <= 100 ? null : { notValid: true };
        } else {
            return true;
        }
    }

    /**
     * validateNotEmptyString
     * @param control
     * @returns {any}
     */
    validateNotEmptyString(control: FormControl) {
        if (control.value.trim() !== '') {
            return null;
        } else {
            return {
                validateNotEmptyString: {
                    valid: false
                }
            };
        }
    }
}
