import { Injectable } from '@angular/core';
import { CommonSharedDataStorageService } from './common-shared-data-storage.service';

@Injectable({
  providedIn: 'root'
})
export class UiPermissionsService {

  private customizedItems = ['master-menu'];
  private sharedData;

  constructor(private sharedDataStore: CommonSharedDataStorageService) {
    this.sharedData = this.sharedDataStore.getSharedData();
  }

  public permissionCheck(permission: string): boolean {
    let rerurnRes = false;
    if (!!this.sharedData && !!this.sharedData['permissions']) {
      const permissionArr = permission.split('|');
      permissionArr.forEach(perm => {
        if (!rerurnRes) {
          rerurnRes = (this.customizedItems.indexOf(perm) !== -1) ? this.checkCustomizedItem(perm) :
            (this.sharedData.permissions.indexOf(perm) !== -1);
        }
      });
      return rerurnRes;
    }
    return false;
  }

  private checkCustomizedItem(item): boolean {
    let allowed = true;
    switch (item) {
      case 'master-menu':
        allowed = this.checkMasterMenuAllowed();
        break;

      default:
        allowed = false;
        break;
    }
    return allowed;
  }

  private checkMasterMenuAllowed(): boolean {
    const tempArray = ['list-activity', 'list-technology', 'list-team'];
    const t = this;
    let matchFound = false;
    tempArray.forEach(element => {
      if (t.sharedData.permissions.indexOf(element) !== -1) {
        matchFound = true;
      }
    });
    return matchFound;
  }
}
