import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { CommonSharedDataStorageService } from '../../services/common-shared-data-storage.service';

@Component({
  selector: 'app-errorpage',
  templateUrl: './errorpage.component.html',
  styleUrls: ['./errorpage.component.scss']
})
export class ErrorpageComponent implements OnInit {

  constructor(
    public sharedData: CommonSharedDataStorageService,
    private router: Router
  ) { }

  ngOnInit() {
  }

  public buttonClick() {
    if (this.sharedData.errorPageButtonType === 'Back') {
      window.history.back();
    } else {
      this.sharedData.errorPageButtonType = 'Back';
      this.router.navigateByUrl('home');
    }
  }

}
