import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { Subject } from 'rxjs/Subject';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';

@Injectable({
  providedIn: 'root'
})
export class NotificationService {

  private notificationTriger = new Subject<any>();
  private notificationTriger$ = this.notificationTriger.asObservable();

  constructor() { }

  public trigerNotification(notification) {
    this.notificationTriger.next({ notification: notification });
  }

  public gettrigerNotification() {
    return this.notificationTriger$;
  }


}
