import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { Subject } from 'rxjs/Subject';
import { HttpClient } from '@angular/common/http';
import { BASE_URL, ApplicationConstant } from '../../constants/constant';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';

@Injectable({
  providedIn: 'root'
})
export class CommonSharedDataStorageService {

  private dataModify = new Subject<any>();
  private dataModify$ = this.dataModify.asObservable();

  private sharedData: any = null;
  private http: HttpClient;
  public errorPageButtonType = 'Back';
  constructor() {

  }

  public getSharedData() {
    return this.sharedData;
  }

  public setSharedData(data: any = null) {
    this.sharedData = data;
    this.dataModify.next(this.sharedData);
  }

  public getSharedDataAsObserveable() {
    return this.dataModify$;
  }

  public async loadData(http: HttpClient) {
    const t = this;
    this.http = http;
    let dataLoaded = false;
      await this.fetchData(http).then(async function (data) {
      t.setSharedData(data['data']);
      dataLoaded = true;
    });
    return dataLoaded;
  }

  public async reloadData() {
    let dataReloaded = false;
    if (this.http !== undefined) {
      const t = this;
      const http = this.http;
      await this.fetchData(http).then(function (data) {
        t.setSharedData(data['data']);
        dataReloaded = true;
      });
      return dataReloaded;
    }
    return dataReloaded;
  }

  public fetchData(http: HttpClient) {
    let endpoint = BASE_URL + ApplicationConstant.COMMON_SERVICE;
    let t = this;
    return new Promise((resolve, reject) => {
      this.http
        .get(endpoint).subscribe(data => { resolve(data); });
    });
  }
}
