import { NgModule, ModuleWithProviders, Directive } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ModalModule } from 'ngx-bootstrap';
import { NotificationService } from './services/notification.service';
import { NotificationsComponent } from './components/notifications/notifications.component';
import { ConfirmationdialogComponent } from './components/confirmationdialog/confirmationdialog.component';
import { InformationdialogComponent } from './components/informationdialog/informationdialog.component';
import { PaginationComponent } from './components/pagination/pagination.component';
import { ErrorpageComponent } from './components/errorpage/errorpage.component';
import { ObjIteratorPipe } from './pipes/obj-iterator.pipe';
import { CommonSharedDataStorageService } from './services/common-shared-data-storage.service';
import { HasPermissionDirective } from './Directives/hasPermission';
import { ValidatorService } from './services/validators.service';
import { SharedFunctionsService } from './services/sharedfunctions.service';


@NgModule({
  imports: [
    CommonModule,
    ModalModule.forRoot(),
  ],
  declarations: [
    NotificationsComponent,
    ConfirmationdialogComponent,
    InformationdialogComponent,
    ErrorpageComponent,
    ObjIteratorPipe,
    PaginationComponent,
    HasPermissionDirective
  ],
  exports: [
    CommonModule,
    NotificationsComponent,
    ConfirmationdialogComponent,
    InformationdialogComponent,
    ErrorpageComponent,
    ObjIteratorPipe,
    PaginationComponent,
    HasPermissionDirective
  ],
})
export class SharedServicesAndComponentsModule {
  static forRoot(): ModuleWithProviders {
    return {
      ngModule: SharedServicesAndComponentsModule,
      providers: [NotificationService, CommonSharedDataStorageService, ValidatorService, ObjIteratorPipe, SharedFunctionsService],
    };
  }
}
