import { Component, OnInit, ViewChild, Output, EventEmitter } from '@angular/core';
import { ModalDirective } from 'ngx-bootstrap/modal';

@Component({
  selector: 'app-confirmationdialog',
  templateUrl: './confirmationdialog.component.html',
  styleUrls: ['./confirmationdialog.component.scss']
})
export class ConfirmationdialogComponent implements OnInit {
  @ViewChild('confirmationWindow') public confirmationWindow: ModalDirective;
  @Output() confirmed: EventEmitter<any> = new EventEmitter<any>();

  private tempData: any = null;
  constructor() { }

  ngOnInit() {
  }

  public presentConfirmationWindow(data: any = {}) {
    this.setTempData(data);
    this.confirmationWindow.show();
  }

  private hideConfirmationWindow() {
    this.setTempData(null);
    this.confirmationWindow.hide();
  }

  public decline() {
    this.setTempData(null);
    this.hideConfirmationWindow();
  }

  public confirm() {
    this.confirmed.emit(this.tempData);
    this.hideConfirmationWindow();
  }

  private setTempData(data: any) {
    this.tempData = null;
    this.tempData = data;
  }

}
