import { Directive, Input, OnChanges, TemplateRef, ViewContainerRef, OnInit } from '@angular/core';
import { CommonSharedDataStorageService } from '../services/common-shared-data-storage.service';
import { UiPermissionsService } from '../services/ui.permissions.service';

@Directive({
    selector: '[hasPermission]'
})

export class HasPermissionDirective implements OnInit {

    constructor(
        private templateRef: TemplateRef<any>,
        private viewContainer: ViewContainerRef,
        private sharedDataStore: CommonSharedDataStorageService,
        private uiPermissionsService: UiPermissionsService
    ) { }

    @Input() public hasPermission;

    ngOnInit() {
        this.setView();
    }

    private setView() {
        if (this.uiPermissionsService.permissionCheck(this.hasPermission)) {

            // If condition is true add template to DOM
            this.viewContainer.createEmbeddedView(this.templateRef);
        } else {
            // Else remove template from DOM
            this.viewContainer.clear();
        }
    }
}
