import { Pipe, PipeTransform } from '@angular/core';
import { SharedFunctionsService } from '../services/sharedfunctions.service';

@Pipe({
  name: 'objIterator'
})
export class ObjIteratorPipe implements PipeTransform {
  constructor(private sharedFunctionsService: SharedFunctionsService) {}

  transform(value: any, args: any[] = null): any {
    const arr = Object.keys(value).map(key =>
      Object.assign({ key }, value[key])
    );
    return this.sharedFunctionsService.customArraySorter(arr, 'name');
  }
}
