import { Component, Input, OnInit, ViewChild, Output, EventEmitter } from '@angular/core';
import { ModalDirective } from 'ngx-bootstrap/modal';

@Component({
  selector: 'app-informationdialog',
  templateUrl: './informationdialog.component.html',
  styleUrls: ['./informationdialog.component.scss']
})
export class InformationdialogComponent implements OnInit {
  @ViewChild('informationdialogWindow') public informationdialogWindow: ModalDirective;
  @Input() public information: string;
  @Output() confirmed: EventEmitter<any> = new EventEmitter<any>();

  private tempData: any = null;
  constructor() { }

  ngOnInit() {
  }

  public presentInformationWindow(information: string) {
    this.setTempData(information);
    this.informationdialogWindow.show();
  }

  private hideInformationWindow() {
    this.setTempData(null);
    this.informationdialogWindow.hide();
  }

  public decline() {
    this.setTempData(null);
    this.hideInformationWindow();
  }

  public confirm() {
    this.confirmed.emit(this.tempData);
    this.hideInformationWindow();
  }

  private setTempData(data: any) {
    this.tempData = null;
    this.tempData = data;
    this.information = data;
  }

}
