import { Component, OnInit } from '@angular/core';
import { NotificationService } from '../../services/notification.service';

@Component({
  selector: 'app-notifications',
  templateUrl: './notifications.component.html',
  styleUrls: ['./notifications.component.scss']
})
export class NotificationsComponent implements OnInit {

  public alertVisibility = false;
  public currentAlert: Object = {};

  constructor(
    private notificationService: NotificationService
  ) { }

  ngOnInit() {
    this.notificationService.gettrigerNotification().subscribe(
      res => {
        this.buildNotificationObject(res.notification);
        this.presentAlert();
      }
    );
  }

  presentAlert() {
    this.alertVisibility = true;
    let cur = this;
    setTimeout(
      function () {
        cur.alertVisibility = false;
      }, 3000);
  }

  private buildNotificationObject(data) {
    let switchData = data.type !== undefined ? data.type : 'info';
    switch (switchData) {
      case 'success':
        this.currentAlert['type'] = 'Success !';
        this.currentAlert['class'] = 'alert alert-success';
        break;
      case 'warning':
       this.currentAlert['type'] = 'Warning !';
       this.currentAlert['class'] = 'alert alert-warning';
        break;
      case 'danger':
      this.currentAlert['type'] = 'Error !';
      this.currentAlert['class'] = 'alert alert-danger';
        break;
      default:
      this.currentAlert['type'] = 'Information !';
      this.currentAlert['class'] = 'alert alert-info';
        break;
    }
    this.currentAlert['message'] = data.message;
  }

}
